*{
    margin: 0;
    box-sizing: border-box;
}
.navbar{
    display: flex;
    justify-content: space-between;
    /* background-color: rgb(71, 199, 249); */
    background-color: white;
    align-items: center;
    padding: 6px;
    
}
.navbar a{
    border-radius: 5px;
    padding: 5px;
    /* background-color: rgb(97, 97, 137); */
    /* color: white; */
    color: black;
    text-emphasis-color: black;
    text-decoration: none;
    padding: 8px;
    font-size: 20px;
}
.navbar a:hover{
   
    /* border-radius: 20px; */
    /* border: 2px solid black; */
    /* text-decoration: underline; */
    color: white;
    background-color: #4CAF50;
    /* background-color: white;  */
}
.navbar a:active{
    text-decoration: underline;
    border-radius: 20px;
    /* border: 2px solid black; */
}

li{
    list-style-type: none;
    display: inline-block;
    margin: 10px;
}
ul{
    display: inline;
    
}